import searchCustom from '~/Pharmacy/searchCustom'
import back from '@/components/back'
export default {
	data () {
		return {
			placeholder: '请输入分组名称',
			arr: [],
			PatientName: '',
			start404: false,
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			start: this.$route.params.start,
			startIf: true,
			checkedNames: [],
		}
	},
	components:{
		searchCustom,
		back
	},
	created(){
		this.httpStart();
		this.startFn();
	},
	methods: {
		circleFn(n,MFItemId){
			if(this.arr[n].checkedNames == 'circle'){
				this.checkedNames.push(MFItemId);
				this.arr[n].checkedNames = 'success'
			}else{
				this.arr[n].checkedNames = 'circle'
				this.checkedNames.map((v,i) => {
					if(this.checkedNames[i] == MFItemId){
						this.checkedNames.splice(i, 1);
					}
				})
			}
		},
		btnFn(){
			let data = '';
			let name = ''
			this.checkedNames.map((v,i) => {
				data += this.checkedNames[i] + ','
			})
			data = data.substring(0,data.length - 1);
			this.arr.forEach(o => {
				this.checkedNames.forEach(a => {
					if(o.MFItemId == a){
						name += o.MFItemName + ','
					}
				})
			})
			name = name.substring(0,name.length - 1);
			const obj = {
				data,
				name
			}
			this.$bridge.callhandler('groups', JSON.stringify(obj))
		},
		startFn(){
			this.start == '2' ? this.startIf = false : this.startIf = true;
		},
		httpStart () {
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/QueryRecordCountByDrId'
			obj.parameter = {
				DrId: this.DrId,
				MFItemName: this.PatientName
			}
			this.$get(obj.url,obj.parameter).then(o => {
				if(o.length == 0 || o == [] || o == undefined || o == null){
					this.start404 = true
				}else{
					this.start404 = false
				}
				o.map((v,n) => {
					o[n].checkedNames = 'circle'
				})
				this.arr = o
			})
		},
		searchValue (v) {
			this.PatientName = v
			this.httpStart()
		},	
		grouapsFn (DrId,MFItemId,MFItemName) {
			this.$store.state.grouapsArr = {
				DrId:this.DrId,
				MFItemId: MFItemId,
				MFItemName: MFItemName
			}
			this.$router.push({ path: '/patient/emphasis/3'})
		}
	}
}



